    "summary": "Forget going out to eat or ordering takeout every time you crave American food. Try making Falafel Burgers with Feta Cucumber Sauce at home. One portion of this dish contains roughly <b>15g of protein</b>, <b>8g of fat</b>, and a total of <b>289 calories</b>. This recipe serves 6. For <b>$1.34 per serving</b>, this recipe <b>covers 22%</b> of your daily requirements of vitamins and minerals. 6395 people were impressed by this recipe. This recipe from Cinnamon Spice and Everything Nice requires onion, ground cumin, dill, and salt. It works well as a main course. From preparation to the plate, this recipe takes about <b>50 minutes</b>. With a spoonacular <b>score of 0%</b>, this dish is very bad (but still fixable). Try <a href=\"https://spoonacular.com/recipes/falafel-burgers-with-feta-tzatziki-sauce-1211031\">Falafel Burgers with Feta Tzatziki Sauce</a>, <a href=\"https://spoonacular.com/recipes/falafel-style-lentil-burgers-with-creamy-feta-sauce-516544\">Falafel Style Lentil Burgers with Creamy Feta Sauce</a>, and <a href=\"https://spoonacular.com/recipes/falafel-style-lentil-burgers-with-creamy-feta-sauce-1329745\">Falafel Style Lentil Burgers with Creamy Feta Sauce</a> for similar recipes.",
