        "pairingText": "Malbec, Merlot, and Zinfandel are my top picks for Burger. Merlot will be perfectly adequate for a classic burger with standard toppings. Bolder toppings call for bolder wines, such as a malbec or peppery zinfandel. One wine you could try is Reginato Malbec rosé. It has 4.7 out of 5 stars and a bottle costs about 17 dollars.",
