                "description": "Malbec seems an unlikely hero for a sparkling wine grape, but when you want your rosé to show some real color and character, then Malbec's your man. Crisp and dry with lovely strawberry, rhubarb flavors and aromas. Just a hint of tannin structure, floral spice, and an amazing elegant finish rounds out the mouth. It speaks of the rich Malbec grape, but never loses its light, refreshing nature. It will give you a good reason to drink sparkling wine with your main course!",
